/**
 * axios
 */
import axios from "axios";
// import { Notification, MessageBox, Message } from 'element-ui'
import storage from "@/utils/storage";
// import { getToken } from '@admin/utils/auth'
let clone: any;
export function jsmethod(qwe: any) {
  if (qwe) {
    clone = qwe;
  }
}

axios.defaults.headers["Content-Type"] = "application/json;charset=utf-8";
// axios
const service = axios.create({
  // axiosbaseURLURL
  baseURL: process.env.VUE_APP_BASE_API,
  // 
  timeout: 10000
});
// request
service.interceptors.request.use(
  config => {
    if (window.location.hash !== "#/" && window.location.hash !== "#/login") {
      if (storage.get("ACCESS_TOKEN")) {
        config.headers["Authorization"] =
          "Bearer " + storage.get("ACCESS_TOKEN"); // token 
      }
    }
    return config;
  },
  error => {
    console.log(111, error);
    Promise.reject(error);
  }
);
// 
service.interceptors.response.use(
  (res: any) => {
    const msg = res.data.msg;
    const status = res.data.status;
    if (!msg && status == 2) {
      if (window.location.hash.indexOf("#/design") != -1) {
        clone();
        if (confirm(",") == true) {
          window.open((window as any).location.origin);
          return true;
        } else {
          return false;
        }
      } else {
        if (confirm(",") == true) {
          (window as any).location.href = (window as any).location.origin;
          return true;
        } else {
          return false;
        }
      }

      //alert("");

      //   MessageBox.confirm(
      //     "",
      //     "",
      //     {
      //       confirmButtonText: "",
      //       cancelButtonText: "",
      //       type: "warning"
      //     }
      //   ).then(() => {
      //     store.dispatch("LogOut").then(() => {
      //       location.reload(); // vue-router bug
      //     });
      //   });
    } else if (msg == true && status == 2) {
      //   Notification.error({
      //     title: res.data.msg
      //   });
      if (res.data.message) {
        alert(res.data.message);
      } else {
        alert("");
      }

      // return Promise.reject("error");
    } else {
      return res;
    }
  },
  error => {
    console.log("err" + error);
    // Message({
    //   message: error.message,
    //   type: "error",
    //   duration: 5 * 1000
    // });
    return Promise.reject(error);
  }
);

export default service;
